@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  scroll-padding-top: 59px;
}

.app {
  display: flex;
  justify-content: center;
}

.app-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.titlemain {
  color: #595da5;
  margin-top: 100px;
  margin-bottom: 10px;
  display: flex;
  font-family: 'Poppins', serif;
  font-size: 50px;
  font-weight: 600;
  justify-content: center;
}


.descriptionmain {
  color: #595da5;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  font-family: 'Poppins', serif;
  font-size: 25px;
  font-weight: 600;
  justify-content: center;
}


.footermain {
  color: #595da5;
  margin-top: 10px;
  margin-bottom: 100px;
  display: flex;
  font-family: 'Poppins', serif;
  font-size: 15px;
  font-weight: 600;
  justify-content: center;
}


.footerlink {
  color: #5467ff;
  margin-left: 4px;
  display: flex;
  font-family: 'Poppins', serif;
  font-size: 15px;
  font-weight: 600;
  justify-content: center;
}