/* For all blocks */
.animated-block {
  align-items: flex-end;
  align-self: flex-end;
  border-radius: 5px;
  box-shadow: 0 5px 13px 1px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  position: relative;
}

.animated-block > span {
  color: whitesmoke;
}

/* Counting sort */
.fixed-array-container {
  align-items: center;
  align-self: flex-end;
  display: flex;
  flex-direction: column;
}

.count {
  font-family: 'Open Sans Semibold', sans-serif;
  font-size: 16px;
  margin-top: 5px;
}

/* Bucket and radix sort */
.oval {
  align-items: center;
  background: linear-gradient(45deg, #287ed0, #5466ff);
  border-radius: 50%;
  box-shadow: 0 5px 13px 1px rgba(0, 0, 0, 0.2);
  color: #d7d7d7;
  display: flex;
  font-size: 16px;
  height: 40px;
  justify-content: center;
  padding: 5px;
  position: relative;
  width: 50px;
}

.decimal > span:nth-child(3) {
  color: white;
  font-weight: bold;
}

.reversed-number {
  align-items: center;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.horiz-arr {
  align-self: center;
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  grid-auto-flow: column;
  justify-content: center;
}

.stack {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-left: 10px;
}

.stack-boxes {
  display: flex;
  flex-direction: column-reverse;
}

.number-with-line {
  border-top: 1px solid black;
  font-family: 'Open Sans Semibold', sans-serif;
  font-size: 15px;
  margin-top: 10px;
