}

.row-container-one {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1.5em;
}

.text {
  align-items: center;
  display: grid;
}

.complexity {
  align-items: center;
  display: grid;
}

.complexity p {
  background-color: #5467ff;
  border: 5px solid #5467ff;
  border-radius: 20px;
  color: #f2f2f2;
  font-family: 'Open Sans Semibold', sans-serif;
  letter-spacing: 1.3px;
  text-align: center;
}

.row-container-two {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  margin-top: 1%;
}

.row-container-two div {
  min-width: 100px;
  text-align: center;
}

.link,
.link:focus {
  color: steelblue;
  font-size: 15px;
  margin-top: 6%;
  text-decoration: underline;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.code-template {
  align-items: center;
  height: 100%;
  margin-left: 4%;
  padding: 0 8% 0;
  width: 110%;
}

.editor {
  background-color: #f1f5fd;
  border: 1px solid #5e9bd1;
  border-top: 3px solid #f1f5fd;
  box-shadow: 0 5px 10px 1px rgba(0, 0, 0, 0.2);
  width: 100% !important;
}

.selector {
  align-items: center;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.select {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  color: white;
  font-family: 'Open Sans Semibold', sans-serif;
  font-size: 16px;
  height: 35px;
  line-height: 35px;
  margin-bottom: 0;
  text-align: center;
}

.select:hover {
  cursor: pointer;
}

.code {
  align-content: center;
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: 1fr 2.5fr;
  justify-items: start;
  margin-left: 4%;
  margin-top: 4%;
  padding: 0 20px 40px;
  -webkit-transform: translateX(3vw);
          transform: translateX(3vw);
}

